import React, { useState } from 'react';
import styled from 'styled-components';

const MainContainer = styled.div`
	width: 100%;
	
	margin: 0 auto;
	padding: 0 16px;
	padding 0 8% 0 8%;
`;

const StepContainer = styled.div`
	display: flex;
	justify-content: space-between;
	position: relative;
	margin-top: 40px;
	:before {
		content: '';
		position: absolute;
		background: var(--app-color-gray-lighter);
		height: 4px;
		width: 100%;
		top: 50%;
		transform: translateY(-50%);
		opacity: 0.3;
		left: 0;
	}
	:after {
		content: '';
		position: absolute;
		background: #4a154b;
		height: 4px;
		width: ${({ width }) => width};
		top: 50%;
		transition: 0.4s ease;
		transform: translateY(-50%);
		left: 0;
	}
`;

const StepWrapper = styled.div`
	position: relative;
	z-index: 1;
`;

const StepStyle = styled.div`
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: #ffffff;
	border: 3px solid ${({ step }) => (step === 'completed' ? 'black' : 'var(--app-color-gray-dark)')};
	transition: 0.4s ease;
	display: flex;
	justify-content: center;
	align-items: center;
`;

const StepCount = styled.span`
	font-size: 19px;
	color: var(--app-color-gray-dark);
	@media (max-width: 600px) {
		font-size: 16px;
	}
`;

const StepsLabelContainer = styled.div`
	position: absolute;
	width: 300px;
	top: 66px;
	left: 50%;
	transform: translate(-50%, -50%);
`;

const StepLabel = styled.span`
	font-size: 10px;
	color: var(--app-color-gray-dark);

	@media (max-width: 800px) {
		font-size: 16px;
	}
	@media (max-width: 600px) {
		font-size: 8px;
	}
`;

const CheckMark = styled.div`
	font-size: 26px;
	font-weight: 600;
	color: #4a154b;
	-ms-transform: scaleX(-1) rotate(-46deg); /* IE 9 */
	-webkit-transform: scaleX(-1) rotate(-46deg); /* Chrome, Safari, Opera */
	transform: scaleX(-1) rotate(-46deg);
`;

const ProgressSteps = props => {
	const { steps, activeStep = 1 } = props;

	const totalSteps = steps.length;

	const width =  `${(100 / (totalSteps - 1)) * (activeStep - 1)}%`;

	return (
		<MainContainer>
			<StepContainer width={width}>
				{steps.map(({ rank, name }) => (
					<StepWrapper key={rank - 1}>
						<StepStyle step={activeStep >= rank - 1 ? 'completed' : 'incomplete'}>{activeStep > rank - 1 ? <CheckMark>L</CheckMark> : <StepCount>{rank - 1}</StepCount>}</StepStyle>
						<StepsLabelContainer>
							<StepLabel key={rank - 1}>{name}</StepLabel>
						</StepsLabelContainer>
					</StepWrapper>
				))}
			</StepContainer>
		</MainContainer>
	);
};

export default ProgressSteps;
